import React, { useEffect, useState, Fragment } from "react";
import { styled, useTheme } from '@mui/material/styles';
import Box from '@mui/material/Box';
import CssBaseline from '@mui/material/CssBaseline';
import Toolbar from '@mui/material/Toolbar';

import Typography from '@mui/material/Typography';
import IconButton from '@mui/material/IconButton';
import MoreVertIcon from '@mui/icons-material/MoreVert';
import BottomNavigation from '@mui/material/BottomNavigation';
import BottomNavigationAction from '@mui/material/BottomNavigationAction';
import HomeIcon from '@mui/icons-material/Home';
import GroupIcon from '@mui/icons-material/Group';
import ArchiveIcon from '@mui/icons-material/Archive';
import Paper from '@mui/material/Paper';
import AppBar from '@mui/material/AppBar';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';

import { useAuthState } from "react-firebase-hooks/auth";
import { auth, db, logout } from "../data/firebase";
import { useNavigate } from "react-router-dom";



import SubFormMain from './subForms/Main';
import SubFormClients from './subForms/Clients';

import { fetchUserName } from '../data/db'

const drawerWidth = 240;


export default function Dashboard() {

    const [user, loading, error] = useAuthState(auth);
    const [name, setName] = useState(null);

    const [value, setValue] = React.useState(0);
    const navigate = useNavigate();

    //menu start
    const [anchorEl, setAnchorEl] = React.useState(null);
    const open = Boolean(anchorEl);
    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };
    const handleClose = (e) => {
        
        if (e == "") logout()


        
        setAnchorEl(null);

    };
    const options = [
        ''
    ];
    //menu ends




    useEffect(async () => {
        if (loading) return;
        if (!user) return navigate("/");

        const un = await fetchUserName(user);
        setName(un);

        //fetchClients()
    }, [user, loading]);

    const theme = useTheme();


    return (
        <>
            {name !== null &&

                <Box sx={{ display: 'flex' }}>
                    <CssBaseline />
                    <AppBar position="fixed">
                        <Toolbar style={{ display: "flex", flexDirection: "row", justifyContent: "space-between" }}>

                            <Typography variant="h6" noWrap component="div">
                                {name ? name : ''}
                            </Typography>
                            <IconButton
                                color="inherit"
                                aria-label="open drawer"
                                onClick={handleClick}
                                edge="start"

                            >
                                <MoreVertIcon />
                            </IconButton>
                            <Menu
                                id="long-menu"
                                MenuListProps={{
                                    'aria-labelledby': 'long-button',
                                }}
                                anchorEl={anchorEl}
                                open={open}
                                onClose={handleClose}
                                PaperProps={{
                                    style: {
                                        maxHeight: 48 * 4.5,
                                        width: '20ch',
                                    },
                                }}
                            > {options.map((option) => (
                                <MenuItem key={option} onClick={(event) => handleClose(option, event)}>
                                    {option}
                                </MenuItem>
                            ))}</Menu>
                        </Toolbar>
                    </AppBar>
                    <Box sx={{ padding: "10px", position: 'fixed', top: "55px", left: 0, right: 0, bottom: "56px", backgroundColor: "gray" }} elevation={3}>
                        {value === 0 && <SubFormMain />}
                        {value === 1 && <SubFormClients />}
                    </Box>

                    <Paper sx={{ position: 'fixed', bottom: 0, left: 0, right: 0 }} elevation={3}>
                        <BottomNavigation
                            showLabels
                            value={value}
                            onChange={(event, newValue) => {
                                setValue(newValue);
                            }}
                        >
                            <BottomNavigationAction icon={<HomeIcon />} />
                            <BottomNavigationAction icon={<GroupIcon />} />
                            <BottomNavigationAction icon={<ArchiveIcon />} />
                            <BottomNavigationAction icon={<ArchiveIcon />} />
                            <BottomNavigationAction icon={<ArchiveIcon />} />

                        </BottomNavigation>
                    </Paper>
                </Box>
            }
        </>
    );
}

